.modal__login, .modal__register {
  width: 100%;
  height: 100%;
  background-color: #0006;
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  overflow: auto;
}

.login, .register {
  background-color: #fff;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.auth__logout-btn {
  display: none;
}

.is-shown {
  display: block;
}

.is-hidden {
  display: none;
}

